import React, {Component} from 'react';

import '../../../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import {AppContext} from '../../../../Context';
class RightCards extends Component{
    static contextType = AppContext;

    componentDidMount(){
        this.context.get_users();
    
    }

    handleUpdate = (id) => {
        this.context.handleUpdate(id,this.username.value,this.adminpwd.value,this.email.value);
    }

    render(){
       return (
    <>
<div class="card">
  <img class="card-img-top" src="data:image/jpeg;base64," alt="Card image cap"></img>
  <div class="card-body">
    <h5 class="card-title">Card title</h5>
    <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
  </div>
  <ul class="list-group list-group-flush">
    <li class="list-group-item">Cras justo odio</li>
    <li class="list-group-item">Dapibus ac facilisis in</li>
    <li class="list-group-item">Vestibulum at eros</li>
  </ul>
  <div class="card-body">
    <a href="#" class="card-link">Card link</a>
    <a href="#" class="card-link">Another link</a>
  </div>
</div>
  </>
       )
      
    }

}

export default RightCards;